/// <reference types='@types/googlemaps' />
import { Component, OnInit, ViewChild } from '@angular/core';
import { SpaceService } from '../../services/space.service';
import { SpaceModel } from '../../models/space.model';
import { SpaceImagesModel } from '../../models/space_images.model';
import { ActivatedRoute, Router } from '@angular/router';
import { Meta } from '@angular/platform-browser';
import { DatepickerOptions } from 'ng2-datepicker';
import * as ptLocale from 'date-fns/locale/pt';
import { environment } from '../../../environments/environment';
import { UserHelper } from '../../helpers/user.helper';
import { JwtHelperService } from '@auth0/angular-jwt';
declare var $: any;
@Component({
  selector: 'app-space-details',
  templateUrl: './space-details.component.html',
  styleUrls: ['./space-details.component.css']
})

export class SpaceDetailsComponent implements OnInit {
  @ViewChild('googleMap') gmapElement: any;
  map: google.maps.Map;
  space: SpaceModel = new SpaceModel();
  relatedSpaces: SpaceModel[] = new Array<SpaceModel>();
  images: string[] = [];
  availability: any[] = [];
  urlToShare = '';
  environment = environment;
  isLogin = false;
  userHelper: UserHelper = new UserHelper();
  helper: JwtHelperService = new JwtHelperService();
  date: Date = new Date();
  relatedSpace: any;
  dateOptions: DatepickerOptions = {
    minYear: 2018,
    maxYear: 2030,
    displayFormat: 'DD [/] MM [/] YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    locale: ptLocale,
    minDate: new Date(Date.now()), // Minimal selectable date
    // maxDate: new Date(),  // Maximal selectable date
    barTitleIfEmpty: 'Selecione uma data',
    placeholder: 'Selecione uma data', // HTML input placeholder attribute (default: '')
    addClass: 'form-control btn-select', // Optional, value to pass on to [ngClass] on the input field
    addStyle: { width: '100%!important' }, // Optional, value to pass to [ngStyle] on the input field
    fieldId: 'my-date-picker', // ID to assign to the input field. Defaults to datepicker-<counter>
    useEmptyBarTitle: false, // Defaults to true. If set to false then barTitleIfEmpty will be disregarded and a date will always be shown
  };
  constructor(public router: Router, public activatedRoute: ActivatedRoute, private spaceService: SpaceService, private metaService: Meta) {
    $(document).ready(function () {
      if ($(window).width() > 992) {
        $(window).scroll(function () {
          if ($(window).scrollTop() > 504) {
            $('.info-block').addClass('block-fixed');
          } else {
            $('.info-block').removeClass('block-fixed');
          }
        });
      }
    });
    this.activatedRoute.params.subscribe(async params => {
      this.urlToShare = `${this.environment.urlFront}details/${params['id']}`;
    });
  }

  ngOnInit() {
    try {
      this.activatedRoute.params.subscribe(async params => {
        const spaceId = params['id'];
        await this.spaceService.postVisit(spaceId);
        const query = { id: spaceId, user_id: undefined };
        if (this.getUserInfo()) {
          query.user_id = this.getUserInfo().id;
        }
        this.space = await this.spaceService.getById(query);
        const resRelated = await this.spaceService.getSimilar(this.space['category'].id, this.space.id);
        this.relatedSpaces = resRelated.spaces;
        this.addImages();
        this.images = [];
        this.space['space_images'].forEach((spaceImage: SpaceImagesModel) => {
          if (spaceImage.main) {
            this.images.unshift(spaceImage.url);
          } else { this.images.push(spaceImage.url); }
        });

        await this.prepareMap();
        await this.prepareCss();
        this.availability = await this.seeAvailability();
      });
    } catch (e) {
      console.log(e);
    }
  }

  goToDetail(spaceId: number) {
    this.router.navigateByUrl(`/details/${spaceId}`);
  }


  price(value) {
    if (value) {
      const valueFormat = value.toFixed(2).split('.');
      valueFormat[0] = `R$ ${valueFormat[0].split(/(?=(?:...)*$)/).join('.')}`;
      return `${valueFormat.join(',')}`;
    }
  }

  prepareMap() {
    const mapProp = {
      center: new google.maps.LatLng(parseFloat(this.space.address.latitude), parseFloat(this.space.address.longitude)),
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP
    };

    this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
    const marker = new google.maps.Marker({ position: mapProp.center });
    marker.setMap(this.map);
  }

  prepareCss() {
    const dateSelector = setInterval(() => {
      const select: any = document.getElementsByClassName('ngx-datepicker-input form-control btn-select ng-untouched ng-pristine ng-valid');
      if (select.length > 0) {
        select[0].style.cssText = 'width: 100%;';
        clearInterval(dateSelector);
      }
    }, 150);
  }

  seeAvailability() {
    const availabilities = [];
    this.space['availability'].forEach(item => {
      if (availabilities.length === 0) {
        availabilities.push(item);
      } else {
        let added = false;
        availabilities.forEach(availa => {
          if (!added) {
            if (availa.hour_init === item.hour_init && availa.hour_end === item.hour_end) {
              availa.day_of_week += ' ' + item.day_of_week;
              added = true;
            }
          }
        });
        if (!added) {
          availabilities.push(item);
        }
      }
    });

    return availabilities;
  }

  shareFacebook() {
    // shareFacebook
    this.metaService.addTag({ name: 'og:image', content: this.images[0] }, true);
    this.metaService.addTag({ name: 'og:title', content: this.space.name }, true);
  }

  parseLinkedinUrl(url) {
    if (url) {
      return url.replace('#', '%23');
    }
  }

  async favoriteSpace() {
    if (this.getUserInfo()) {
      const userInfo = this.getUserInfo();
      const userFavoriteModel = {
        space_id: undefined,
        user_id: undefined
      };
      userFavoriteModel.space_id = this.space.id;
      userFavoriteModel.user_id = userInfo.id;

      try {
        let favorite;
        if (this.space.liked) {
          favorite = await this.spaceService.deleteFavoriteSpace(userFavoriteModel);
        } else {
          favorite = await this.spaceService.favoriteSpace(userFavoriteModel);
        }
        const space = await this.spaceService.getById({ id: this.space.id, user_id: userInfo.id });
        this.space.liked = space.liked;
      } catch (e) {
        console.log(e);
      }

    } else {
      this.loginModal();
    }
  }

  getUserInfo() {
    if (localStorage.getItem('token-user')) {
      if (this.userHelper.verifyToken(localStorage.getItem('token-user'))) {
        const userInfo = this.helper.decodeToken(localStorage.getItem('token-user'));
        return userInfo;
      }
    }

    return undefined;

  }

  async loginModal() {
    this.isLogin = !this.isLogin;
    const userInfo = this.getUserInfo();
    if (userInfo) {
      try {
        if (!this.relatedSpace) {
          const favoriteSpace = await this.favoriteSpace();
        } else {
          const favoriteSpace = await this.favoriteRelated(this.relatedSpace);
        }
        if (userInfo) {
          const space = await this.spaceService.getById({ id: this.space.id, user_id: userInfo.id });
          this.space.liked = space.liked;
        }
      } catch (e) {
        console.log(e);
      }
    }
  }

  async favoriteRelated(space: SpaceModel) {
    this.relatedSpace = space;
    if (this.getUserInfo()) {
      const userInfo = this.getUserInfo();
      const userFavoriteModel = {
        space_id: undefined,
        user_id: undefined
      };
      userFavoriteModel.space_id = this.relatedSpace.id;
      userFavoriteModel.user_id = userInfo.id;

      try {
        let favorite;
        if (this.relatedSpace.liked) {
          favorite = await this.spaceService.deleteFavoriteSpace(userFavoriteModel);
        } else {
          favorite = await this.spaceService.favoriteSpace(userFavoriteModel);
        }
        const spaceRes = await this.spaceService.getById({ id: this.relatedSpace.id, user_id: userInfo.id });
        space.liked = spaceRes.liked;
      } catch (e) {
        console.log(e);
        const spaceRes = await this.spaceService.getById({ id: this.relatedSpace.id, user_id: userInfo.id });
        space.liked = spaceRes.liked;
        const resRelated = await this.spaceService.getSimilar(this.space['category'].id, this.space.id);
        this.relatedSpaces = resRelated.spaces;
        this.addImages();
      }

    } else {
      this.loginModal();
    }
  }

  addImages() {
    this.relatedSpaces.forEach(related => {
      related.images = [];
      related.space_images.forEach(image => {
        if (image.main) {
          related.images.unshift({ url: image.url, clickAction: () => { this.router.navigate(['/details', related.id]); window.scrollTo({ top: 0, behavior: 'smooth' }) } })
        } else { related.images.push({ url: image.url, clickAction: () => { this.router.navigate(['/details', related.id]); window.scrollTo({ top: 0, behavior: 'smooth' }) } }); }
      });
    });
  }
}
