import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SpaceDetailsComponent } from './space-details.component';
import { SharedModule } from '../../shared/shared.module';
import { NgDatepickerModule } from 'ng2-datepicker';


export const SpaceDetailsRoutes: Routes = [{
  path: '',
  data: {
    heading: 'SpaceDetails'
  },
  component: SpaceDetailsComponent
}];

@NgModule({
  imports: [
    CommonModule,
    NgDatepickerModule,
    RouterModule.forChild(SpaceDetailsRoutes),
    SharedModule
  ],
  declarations: [SpaceDetailsComponent]
})
export class SpaceDetailsModule { }



















